import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";
export default {
  name: "HallAdvertiseConf",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    /*校验手机号*/
    let checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("手机号不能为空"));
      } else {
        if (/^[1][345789]\d{9}$/.test(value)) {
          callback();
        } else {
          return callback(new Error("请输入正确的手机号"));
        }
      }
    };
    return {
      pageNum: 1,
      pageSize: 5,
      total: 0,
      formLabelWidth: "120px",
      value: true,
      dialogModifyVisible: false,
      dialogVisible: false,
      labelPosition: "left", //左对齐
      activeName: "first",
      tableStyle: [
        { label: "渠道名称", prop: "channel_name", width: "" },
        { label: "KEY", prop: "channel_name", width: "" },
        { label: "文字一", prop: "content", width: "" },
        { label: "文字二", prop: "content", width: "" },
        { label: "文字三", prop: "content", width: "" },
        { label: "操作时间", prop: "create_time", width: "" },
        { label: "操作", prop: "action", width: "" }
      ],
      tableData: [
        {
          channel_id: "10012",
          channel_name: "主包",
          content: "",
          status: "启用",
          create_time: "2020-01-01 12:00:00",
          action: [
            { label: "编辑", type: "edit" },
            { label: "删除", type: "delete" }
          ]
        },
        {
          channel_id: "10012",
          channel_name: "主包",
          content: "",
          status: "启用",
          create_time: "2020-01-01 12:00:00",
          action: [
            { label: "编辑", type: "edit" },
            { label: "删除", type: "delete" }
          ]
        },
        {
          channel_id: "10012",
          channel_name: "主包",
          content: "",
          status: "启用",
          create_time: "2020-01-01 12:00:00",
          action: [
            { label: "编辑", type: "edit" },
            { label: "删除", type: "delete" }
          ]
        }
      ],
      records: [],
      pageInfo: new PageInfo(1, [5, 10, 15, 20], 6),
      dialogFormVisible: false,
      jumpposOpts: [
        { label: "vip", value: "vip" },
        { label: "全民代理", value: "agent" },
        { label: "客服", value: "server" },
        { label: "兑换", value: "exchange" },
        { label: "充值", value: "recharge" },
        { label: "活动", value: "active" },
        { label: "绑定手机", value: "bind_phone" },
        { label: "返水", value: "return" },
        { label: "vip福利", value: "vip_reward" },
        { label: "签到", value: "attendance" }
      ],
      form: {
        channel_name: "",
        channel_key: "",
        word1: "",
        word1_type: "general",
        word1_url: "",
        jump_position: "",
        name: "",
        region: ""
      },
      form2: {
        name: ""
      },
      formLabelWidth: "100px",
      fileList: []
    };
  },
  methods: {
    getAdvertiseConfList() {
      this.$http.get('lobby/flyer',{
        params: {
          page: 1,
          limit: 10
        }
      }).then(res => {
        console.log(res)
      })
    },
    /**搜索*/
    search() {
      let data = this.format,
        user_id = 1000;
      this.userList(data, user_id);
    },
    handelClick() {
      this.dialogModifyVisible = true;
    },
    pageNumFn(val) {
      console.log(val, this.pageSize, this.pageNum);
      this.pageNum = val;
    },
    pageSizeFn(val) {
      console.log(val);
      this.pageSize = val;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    }
  },
  watch: {
    "form.word1_type": function(newVal, oldVal) {
      console.log(newVal);
      if (newVal === "copy" || "jump_webpage" && newVal !== "general") {
        this.$nextTick(() => {
          this.form.word1_url = this.form.word1;
        });
      } else {
        this.$nextTick(() => {
          this.form.word1_url = "";
        });
      }
    }
  },
  mounted() {
    this.getAdvertiseConfList()
  }
};



